import React, { useState } from "react";
import { ReactComponent as ArrowRightIcon } from "../imgs/external-link.svg";

type NewsData = {
  content: string;
  url: string;
};

type NewsGridProps = {
  news: NewsData[];
};

const NewsWest: React.FC<NewsGridProps> = ({ news }) => {
  const [selectedRows, setSelectedRows] = useState<number[]>([]);

  const toggleSelectedRow = (index: number) => {
    if (selectedRows.includes(index)) {
      setSelectedRows(selectedRows.filter((i) => i !== index));
    } else {
      setSelectedRows([...selectedRows, index]);
    }
  };

  return (
    <div className="news-item">
      <h1 className="text-2xl font-bold mb-2 text-center w-full">
        Jeugdjournaal
      </h1>
      <div className="table-container">
        <table>
          <tbody>
            {news.map((data, index) => (
              <tr key={data.content}>
                <td className="relative">
                  <button
                    className={`absolute top-1/2 left-0 transform -translate-y-1/2 flex items-center justify-center rounded-full w-9 h-9 bg-transparent focus:outline-none transition-all duration-300 ${
                      selectedRows.includes(index)
                        ? "text-green-500"
                        : "text-gray-600"
                    }`}
                    onClick={() => toggleSelectedRow(index)}
                    style={{ outline: "none" }} // Add this line
                  >
                    {selectedRows.includes(index) ? (
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="h-5 w-5"
                        viewBox="0 0 20 20"
                        fill="none"
                        stroke="currentColor"
                      >
                        <circle cx="10" cy="10" r="9" strokeWidth="2" />
                        <path
                          d="M6 10.75L8.5 13.25L14 7.75"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="2"
                        />
                      </svg>
                    ) : (
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="h-5 w-5"
                        viewBox="0 0 20 20"
                        fill="none"
                        stroke="currentColor"
                      >
                        <circle cx="10" cy="10" r="9" strokeWidth="2" />
                        <path
                          d="M6 10.75L8.5 13.25L14 7.75"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="2"
                          stroke="gray"
                        />
                      </svg>
                    )}
                  </button>

                  <div className="pl-8">
                    {data.content}{" "}
                    <a href={data.url} target="_blank">
                      <ArrowRightIcon className="arrow-right-icon" />
                    </a>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div>
        {selectedRows.length > 0 && (
          <p className="text-gray-500 mt-4">
            Selected topics:{" "}
            {selectedRows.map((index) => (
              <React.Fragment key={index}>
                {news[index].content}
                <br />
              </React.Fragment>
            ))}
          </p>
        )}
      </div>
    </div>
  );
};

export default NewsWest;
