import React, { useState } from "react";

type CalendarData = {
  week: string;
  monday: string;
  tuesday: string;
  wednesday: string;
  thursday: string;
  friday: string;
  weekend: string;
};

type CalendarGridProps = {
  calendar: CalendarData[];
};

const Calendar: React.FC<CalendarGridProps> = ({ calendar }) => {
  const [selectedCells, setSelectedCells] = useState<boolean[][]>(
    Array.from({ length: calendar.length }, () =>
      Array.from({ length: 7 }, () => false)
    )
  );

  const toggleSelectedCell = (rowIndex: number, cellIndex: number) => {
    setSelectedCells((prevSelectedCells) =>
      prevSelectedCells.map((row, i) =>
        i === rowIndex
          ? row.map((cell, j) => (j === cellIndex ? !cell : cell))
          : row
      )
    );
  };

  const Checkmark = ({ isSelected }: { isSelected: boolean }) => (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      className="h-5 w-5 absolute top-1/2 left-0 transform -translate-y-1/2"
      viewBox="0 0 20 20"
      fill="none"
      stroke={isSelected ? "currentColor" : "gray"}
    >
      <circle cx="10" cy="10" r="9" strokeWidth="2" />
      <path
        d="M6 10.75L8.5 13.25L14 7.75"
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth="2"
        stroke={isSelected ? "currentColor" : "gray"}
      />
    </svg>
  );

  return (
    <>
      <h1 className="text-2xl font-bold mb-2 text-center w-full">Kalender</h1>
      <div className="no-bullet-point" style={{ textAlign: "center" }}>
        <div className="responsive-table">
          <table
            className="centered-table"
            style={{
              borderCollapse: "collapse",
            }}
          >
            <thead>
              <tr>
                <th style={{ border: "1px solid #dee2e6", padding: "0.5rem" }}>
                  Week
                </th>
                <th style={{ border: "1px solid #dee2e6", padding: "0.5rem" }}>
                  Maandag
                </th>
                <th style={{ border: "1px solid #dee2e6", padding: "0.5rem" }}>
                  Dinsdag
                </th>
                <th style={{ border: "1px solid #dee2e6", padding: "0.5rem" }}>
                  Woensdag
                </th>
                <th style={{ border: "1px solid #dee2e6", padding: "0.5rem" }}>
                  Donderdag
                </th>
                <th style={{ border: "1px solid #dee2e6", padding: "0.5rem" }}>
                  Vrijdag
                </th>
                <th style={{ border: "1px solid #dee2e6", padding: "0.5rem" }}>
                  Weekend
                </th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {calendar.map((data, rowIndex) => (
                <tr key={data.week}>
                  {[
                    data.week,
                    data.monday,
                    data.tuesday,
                    data.wednesday,
                    data.thursday,
                    data.friday,
                    data.weekend,
                  ].map((value, cellIndex) => {
                    const hasAlphaChars = /[a-zA-Z]/.test(value);

                    return (
                      <td
                        key={cellIndex}
                        className="relative"
                        data-th={
                          [
                            "Week",
                            "Maandag",
                            "Dinsdag",
                            "Woensdag",
                            "Donderdag",
                            "Vrijdag",
                            "Weekend",
                          ][cellIndex]
                        }
                        style={{
                          border: "1px solid #dee2e6",
                          padding: "0.5rem",
                        }}
                      >
                        <div style={{ display: "flex", alignItems: "center" }}>
                          <span style={{ paddingRight: "0.5rem" }}>
                            {value}
                          </span>
                          {hasAlphaChars && (
                            <button
                              className={`transform flex items-center justify-center rounded-full w-9 h-9 bg-transparent focus:outline-none transition-all duration-300 ${
                                selectedCells[rowIndex][cellIndex]
                                  ? "text-green-500"
                                  : "text-gray-600"
                              }`}
                              onClick={() =>
                                toggleSelectedCell(rowIndex, cellIndex)
                              }
                              style={{ outline: "none" }}
                            >
                              <Checkmark
                                isSelected={selectedCells[rowIndex][cellIndex]}
                              />
                            </button>
                          )}
                        </div>
                      </td>
                    );
                  })}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
      <style>{`
        .responsive-table {
          overflow-x: auto;
        }

        @media screen and (max-width: 767px) {
          table,
          thead,
          tbody,
          th,
          td,
          tr {
            display: block;
          }

          thead tr {
            position: absolute;
            top: -9999px;
            left: -9999px;
          }

          tr {
            border: 1px solid #dee2e6;
            margin-bottom: 1rem;
          }

          td {
            border: none;
            border-bottom: 1px solid #dee2e6;
            position: relative;
            padding-left: 50%;
          }

          td:before {
            position: absolute;
            top: 6px;
            left: 6px;
            width: 45%;
            padding-right: 10px;
            white-space: nowrap;
            content: attr(data-th);
            font-weight: bold;
          }
        }
      `}</style>
      <div>
        {selectedCells.some((row) => row.some((cell) => cell)) && (
          <p className="text-gray-500 mt-4">
            {selectedCells
              .flatMap((row, rowIndex) =>
                row.map((cell, cellIndex) =>
                  cell
                    ? {
                        week: calendar[rowIndex].week,
                        day: [
                          "week",
                          "monday",
                          "tuesday",
                          "wednesday",
                          "thursday",
                          "friday",
                          "weekend",
                        ][cellIndex],
                        value: [
                          calendar[rowIndex].week,
                          calendar[rowIndex].monday,
                          calendar[rowIndex].tuesday,
                          calendar[rowIndex].wednesday,
                          calendar[rowIndex].thursday,
                          calendar[rowIndex].friday,
                          calendar[rowIndex].weekend,
                        ][cellIndex],
                      }
                    : null
                )
              )
              .filter(
                (item): item is { week: string; day: string; value: string } =>
                  item !== null
              )
              .map((item, index) => (
                <React.Fragment key={index}>
                  {item.value}
                  <br />
                </React.Fragment>
              ))}
          </p>
        )}
      </div>
    </>
  );
};

export default Calendar;
