import React, { useEffect, useRef } from "react";
import Map from "ol/Map";
import View from "ol/View";
import TileLayer from "ol/layer/Tile";
import OSM from "ol/source/OSM";
import { fromLonLat } from "ol/proj";
import Overlay from "ol/Overlay";
import Feature from "ol/Feature";
import Point from "ol/geom/Point";
import { Vector as VectorSource } from "ol/source";
import { Vector as VectorLayer } from "ol/layer";
import Circle from "ol/style/Circle";
import Fill from "ol/style/Fill";
import Style from "ol/style/Style";
import "ol/ol.css";

const Legend: React.FC = () => {
  return (
    <div
      style={{
        position: "absolute",
        right: "10px",
        top: "10px",
        backgroundColor: "white",
        padding: "10px",
        borderRadius: "5px",
        border: "1px solid black",
      }}
    >
      <div>
        <span
          style={{
            display: "inline-block",
            width: "10px",
            height: "10px",
            backgroundColor: "green",
            borderRadius: "50%",
            marginRight: "5px",
          }}
        ></span>
        Het Lichtbaken
      </div>
      <div>
        <span
          style={{
            display: "inline-block",
            width: "10px",
            height: "10px",
            backgroundColor: "blue",
            borderRadius: "50%",
            marginRight: "5px",
          }}
        ></span>
        Politie
      </div>
      <div>
        <span
          style={{
            display: "inline-block",
            width: "10px",
            height: "10px",
            backgroundColor: "red",
            borderRadius: "50%",
            marginRight: "5px",
          }}
        ></span>
        Brandweer
      </div>
      <div>
        <span
          style={{
            display: "inline-block",
            width: "10px",
            height: "10px",
            backgroundColor: "yellow",
            borderRadius: "50%",
            marginRight: "5px",
          }}
        ></span>
        Ambulance
      </div>
    </div>
  );
};

type EmergenciesData = {
  address: string;
  description: string;
  type: "Politie" | "Brandweer" | "Ambulance";
  url: string;
};

type EmergenciesGridProps = {
  emergencies: EmergenciesData[];
};

async function geocodeAddress(address: string) {
  const url = new URL("https://nominatim.openstreetmap.org/search");
  url.searchParams.set("format", "json");
  url.searchParams.set("q", address);

  const response = await fetch(url.toString());
  const results = await response.json();

  if (results.length === 0) {
    throw new Error(`No results found for address: ${address}`);
  }

  const { lat, lon } = results[0];
  return { latitude: parseFloat(lat), longitude: parseFloat(lon) };
}

const getDotStyle = (type: "Politie" | "Brandweer" | "Ambulance") => {
  let color: string;

  switch (type) {
    case "Politie":
      color = "blue";
      break;
    case "Brandweer":
      color = "red";
      break;
    case "Ambulance":
      color = "yellow";
      break;
    default:
      color = "black";
  }

  return new Style({
    image: new Circle({
      radius: 5,
      fill: new Fill({
        color: color,
      }),
    }),
  });
};

const Emergencies: React.FC<EmergenciesGridProps> = ({ emergencies }) => {
  const mapRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!mapRef.current) return;

    const vectorSource = new VectorSource();

    const fetchCoordinatesAndCreateFeatures = async () => {
      for (const emergency of emergencies) {
        try {
          const coordinates = await geocodeAddress(emergency.address);
          const feature = new Feature({
            geometry: new Point(
              fromLonLat([coordinates.longitude, coordinates.latitude])
            ),
            description: emergency.description,
            url: emergency.url, // Add the URL to the feature
          });

          feature.setStyle(getDotStyle(emergency.type));
          vectorSource.addFeature(feature);
        } catch (error) {
          console.error(`Error geocoding address: ${emergency.address}`, error);
        }
      }
    };

    fetchCoordinatesAndCreateFeatures();

    const centerFeature = new Feature({
      geometry: new Point(fromLonLat([4.2420664, 52.059551])),
      description: "Het Lichtbaken",
    });

    centerFeature.setStyle(
      new Style({
        image: new Circle({
          radius: 7,
          fill: new Fill({
            color: "green",
          }),
        }),
      })
    );

    vectorSource.addFeature(centerFeature);

    const vectorLayer = new VectorLayer({
      source: vectorSource,
    });

    const map = new Map({
      target: mapRef.current,
      layers: [
        new TileLayer({
          source: new OSM(),
        }),
        vectorLayer,
      ],
      view: new View({
        center: fromLonLat([4.2420664, 52.059551]),
        zoom: 15,
      }),
    });

    const descriptionOverlay = new Overlay({
      element: (() => {
        const div = document.createElement("div");
        div.style.backgroundColor = "white";
        div.style.padding = "5px";
        div.style.border = "1px solid black";
        div.style.borderRadius = "4px";
        div.style.pointerEvents = "none";
        return div;
      })(),
      positioning: "bottom-center",
      offset: [0, -10],
      stopEvent: false,
    });
    map.addOverlay(descriptionOverlay);

    map.on("pointermove", (event) => {
      const features = map.getFeaturesAtPixel(event.pixel);
      const feature = features && features.length > 0 ? features[0] : null;

      if (feature && feature.get("description")) {
        descriptionOverlay.setPosition(event.coordinate);
        const element = descriptionOverlay.getElement();
        if (element) {
          element.innerText = feature.get("description");
        }
      } else {
        descriptionOverlay.setPosition(undefined);
      }
    });

    map.on("click", (event) => {
      const features = map.getFeaturesAtPixel(event.pixel);
      const feature = features && features.length > 0 ? features[0] : null;

      if (feature && feature.get("description")) {
        window.open(feature.get("url"), "_blank");
      }
    });

    return () => {
      map.dispose();
    };
  }, [emergencies]);

  return (
    <div
      style={{
        position: "relative",
        width: "100%",
        height: "100%",
      }}
    >
      <div ref={mapRef} style={{ width: "100%", height: "100%" }} />
      <Legend />
    </div>
  );
};

export default Emergencies;
